* {
    margin: 0;
    padding: 0;
}

.main-home-main {
    display: flex;
    flex-direction: column;
}

.main-home {
    width: 100%;
    height: 100vh;
}

.video-home {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -5;
    object-fit: cover;
}

.my-about {
    width: 100%;
    height: 100%;
    /* position: absolute; */
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    color: #fff;
    z-index: -1;
    /* font-size: 3rem; */
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    -webkit-animation: slide-in 1s;
            animation: slide-in 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    /* border: 2px solid red; */
}

.my-about h1 {
    margin-left: 3vw;
    font-family: 'Bree Serif', serif;
    box-sizing: border-box;
    font-weight: 400;
    font-size: 6.3rem;
    line-height: 7.5rem;
    position: relative;
}

.place {
    background: -webkit-linear-gradient(242deg, #fff 3.43%, #fff066 15.69%, #ffa31a 55.54%, #ff4b9c 99%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 300%;
}

.place:hover {
    background: -webkit-linear-gradient(45deg, #fb0094 10%, #2200ff 65%, #ff0000 99%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 300%;
}

.rubber-letter {
    transition: 0.2s ease-in-out color;
}

.rubber-letter:hover {
    display: inline-block;
    cursor: pointer;
    -webkit-animation: rubberlatter 1s ease-out;
            animation: rubberlatter 1s ease-out;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

.gray-text {
    font-size: 2.4rem;
    letter-spacing: 4px;
    margin-left: 3vw;
    margin-top: 3rem;
    display: block;
    color: #8d8d8d;
    font-family: 'Roboto', sans-serif;
}

.scroll-down {
    margin-right: 1vw;
    display: flex;
    bottom: 0;
    right: 0;
    position: absolute;
    -webkit-writing-mode: vertical-rl;
            writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
    font-size: 1em;
}

.scroll-down i {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    margin-right: 0.3vw;
}

.scroll-down p {
    -webkit-animation: bounce 1s ease-in-out infinite;
            animation: bounce 1s ease-in-out infinite;
    letter-spacing: 2px;
}

.about {
    margin-bottom: 9rem;
}

@-webkit-keyframes bounce {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

    10% {
        -webkit-transform: translateY(-5%);
                transform: translateY(-5%);
    }

    20% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }

    30% {
        -webkit-transform: translateY(-15%);
                transform: translateY(-15%);
    }

    40% {
        -webkit-transform: translateY(-20%);
                transform: translateY(-20%);
    }

    50% {
        -webkit-transform: translateY(-30%);
                transform: translateY(-30%);
    }

    60% {
        -webkit-transform: translateY(-20%);
                transform: translateY(-20%);
    }

    70% {
        -webkit-transform: translateY(-15%);
                transform: translateY(-15%);
    }

    80% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }

    90% {
        -webkit-transform: translateY(-5%);
                transform: translateY(-5%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes bounce {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

    10% {
        -webkit-transform: translateY(-5%);
                transform: translateY(-5%);
    }

    20% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }

    30% {
        -webkit-transform: translateY(-15%);
                transform: translateY(-15%);
    }

    40% {
        -webkit-transform: translateY(-20%);
                transform: translateY(-20%);
    }

    50% {
        -webkit-transform: translateY(-30%);
                transform: translateY(-30%);
    }

    60% {
        -webkit-transform: translateY(-20%);
                transform: translateY(-20%);
    }

    70% {
        -webkit-transform: translateY(-15%);
                transform: translateY(-15%);
    }

    80% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }

    90% {
        -webkit-transform: translateY(-5%);
                transform: translateY(-5%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes rubberlatter {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }

    30% {
        -webkit-transform: scale3d(1.25, 0.75, 1);
                transform: scale3d(1.25, 0.75, 1);
    }

    40% {
        -webkit-transform: scale3d(0.75, 1.25, 1);
                transform: scale3d(0.75, 1.25, 1);
    }

    50% {
        -webkit-transform: scale3d(1.15, 0.85, 1);
                transform: scale3d(1.15, 0.85, 1);
    }

    65% {
        -webkit-transform: scale3d(0.95, 1.05, 1);
                transform: scale3d(0.95, 1.05, 1);
    }

    75% {
        -webkit-transform: scale3d(1.05, 0.95, 1);
                transform: scale3d(1.05, 0.95, 1);
    }

    100% {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }
}

@keyframes rubberlatter {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
