body {
  font-family: 'Roboto', sans-serif;
  background: #000;
}
* {
  margin: 0;
  padding: 0;
}
.navigation
{
  position: fixed;
  background: transparent;
  display: flex;
  justify-content: space-between;
  margin: 0;
  width: 100vw;
  height: 15vh;
  z-index: 11;
}
.navigation p{
  font-family: 'Fredoka One', cursive;
  font-size: 2.5rem;
  margin-left: 3vw;
  margin-right: auto;
  margin-top: 3.4vh;
  border-radius: 0.5rem;
  font-weight: 700;
  /* background:-webkit-linear-gradient( 60deg,#fff,#fb0094,#ff0000,#fff,#fb0094,#ff0000,#fff) ; */
  background: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 300%;
  /* animation: animate 5s linear infinite; */
  /* color: #fff; */
  display: inline-block;
}
.navigation p:hover{
  background: #ff020296;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 300%;
}
.menu-bar{
  box-shadow: -5px -4px 9px 2px rgb(31 31 31 / 60%), 5px 6px 8px rgb(104 132 157 / 23%);
  margin-right: 3vw;
  margin-top: 3.4vh;
  width:45px;
  height: 45px;
  border-radius: 0.5rem;
  background: #ffffff10;
  color: #ffffffc0;
  padding: 5px;
  min-width: 45px;
  min-height: 45px;
  cursor: pointer;
  font-size: 2.45rem;
  display: flex;
}
.menu-bar:hover{
  color: #ff020296;
}
.menu-bar i{
  margin: auto;
}
.nav-link i {
  margin-right: 10px;
}


.navbars-mainbg {
  z-index: 112;
  position: fixed;
  margin-top: 15vh;
  padding: 0px;
  width: 0%;
  height: 90vh;
  transition: all 0.7s ease-in-out;
  background-color: #000;
}
  
#navbarsSupportedContent {
  overflow: hidden;
  display: flex;
  width: 100%;
  height: 90vh;
  justify-content: center;
}
#navbarsSupportedContent ul {
  padding: 0px;
  margin: auto;
  font-weight: 500;
}

#navbarsSupportedContent li {
  position: relative;
  list-style-type: none;
  margin: 10px 5px;
  width: 80vw;
  text-align: center;
  border-radius: 10px;
  background: #ffffff10;
}
#navbarsSupportedContent ul li span{
  display: block;
  position: absolute;
  background: #ffffff6e;
}
#navbarsSupportedContent ul li:hover span:nth-child(1){
    left: 0;
    top: 50%;
    width: 100%;
    height: 1px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
#navbarsSupportedContent ul li::before span:nth-child(1) {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: right;
          transform-origin: right;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
#navbarsSupportedContent ul li:hover span:nth-child(2){
    left: 0;
    bottom: 50%;
    width: 100%;
    height: 1px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: right;
            transform-origin: right;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
#navbarsSupportedContent ul li::after span:nth-child(2) {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

#navbarsSupportedContent ul li a {
  text-decoration: none;
  font-size: 17px;
  display: block;
  padding: 10px 10px;
  position: relative;
  color: #fff;
}
#navbarsSupportedContent ul li a:hover{
  background: -webkit-linear-gradient( 45deg,#fb0094,#2200ff ,#ff0000, #0000ff, #f700ff94, #ff0080, #2200ff, #ff00ff, #ff0000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 300%;
  -webkit-animation: animate 20s linear infinite;
          animation: animate 20s linear infinite;
}

@-webkit-keyframes animate {
    to {
        background-position: 300%;
    }
}

@keyframes animate {
    to {
        background-position: 300%;
    }
}

